.MainWindow {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

    .MainWindow .GameElement {
        position: relative;
        top: 0;
        -ms-flex-positive: 1;
            flex-grow: 1;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-line-pack: center;
            align-content: center;
    }

        .MainWindow .GameElement .GameDisplayComponent {
            position: absolute;
            bottom:0;
            height: calc(100% * 52 / 55);
            width: calc(100% * 52 / 55);
            margin-left: calc(50% * 3 / 55);
            overflow:hidden;
        }

.InActive {
    display:none;
}



